import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppAccessTokenReceived':
					//this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.data.access_token);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		connect.send("VKWebAppGetAuthToken", {"app_id": 6851549, "scope": "notify,friends"});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	pay = (e) => {
		connect.send("VKWebAppCallAPIMethod", {"method": "users.get", "params": {"user_ids": "248761329", "v":"5.92", "access_token":"c80ce7dcc80ce7dcc80ce7dcb9c8646c01cc80cc80ce7dc94507aa8539a94848b3bcf95"}});
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} pay={this.pay} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;



